import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthFacade } from '@feature/auth/auth.facade';

@Component({
    selector: 'findqo-instant-login',
    templateUrl: './instant-login.page.html',
    styleUrls: ['./instant-login.page.scss'],
    standalone: false
})
export class InstantLoginPage implements OnInit {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		private route: ActivatedRoute,
		private authFacade: AuthFacade,
		private router: Router
	) {}

	ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.route.queryParams.subscribe((queryParams) => {
				if (queryParams['authToken'] && queryParams['type']) {
					this.authFacade.instantLoginAndRedirect(queryParams);
				} else {
					this.router.navigate(['/']);
				}
			});
		}
	}
}
